import Vue from 'vue'
import Router from 'vue-router'
import no1 from './router/no1.vue'
import no2 from './router/no2.vue'
import no3 from './router/no3.vue'
import no7 from './router/no7.vue'
import no4 from './router/no4.vue'
import no5 from './router/no5.vue'
import no6 from './router/no6.vue'
import no8 from './router/no8.vue'
import no9 from './router/no9.vue'

Vue.use(Router)

var routes = [
	{
	  path:'/',
	  name:'one',
	  component:no1
	},
	{
	  path:'/no2',
	  name:'two',
	  component:no2
	},
	{
	  path:'/no3',
	  name:'three',
	  component:no3
	},
	{
		path:'/no7',
		name:'no7',
		component:no7
	},
	{
	  path:'/no4',
	  name:'four',
	  component:no4
	},
	{
	  path:'/no5',
	  name:'five',
	  component:no5
	},
	{
	  path:'/no6',
	  name:'no6',
	  component:no6
	},
	{
	  path:'/no8',
	  name:'no8',
	  component:no8
	},
	{
	  path:'/no9',
	  name:'no9',
	  component:no9
	}
]

export default new Router({
	routes
})



