import vue from 'vue'
import Vuex from 'vuex'
// 全局
vue.use(Vuex)
// 引入
var state = {
	arr:[]
}

var getters = {
	g_arr(state){
		return state.arr
	},
	zongji(state){
		var sum=0;
		for(var i=0;i<state.arr.length;i++){
			sum+=state.arr[i].price*state.arr[i].s
		}
		return sum
	}
	
}

var mutations = {
	a_arr(state,n_arr){
		location.href="#/no7";
		var type=true;
		for(var i=0;i<state.arr.length;i++){
			if(state.arr[i].info==n_arr.info){
				type=false
				state.arr[i].s++
			}
		}
		if (type) {
			n_arr.s=1
			state.arr.push(n_arr)
			
		}
	},
	m_jian(state,info){
		for(var i=0;i<state.arr.length;i++){
			if(state.arr[i].info==info){
				if(state.arr[i].s>1){
					state.arr[i].s--
				}
			}
		}
	},
	m_jia(state,info){
		for(var i=0;i<state.arr.length;i++){
			if(state.arr[i].info==info){
				state.arr[i].s++
			}
		}
	},
	m_schu(state,info){
		for(var i=0;i<state.arr.length;i++){
			if(state.arr[i].info==info){
				state.arr.splice(i,1)
			}
		}
	}
}

var actions={
	btn({commit},n_arr){
		commit('a_arr',n_arr)
	},
	jian({commit},info){
		commit('m_jian',info)
	},
	jia({commit},info){
		commit('m_jia',info)
	},
	schu({commit},info){
		commit('m_schu',info)
	}
}
// 暴露
export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions
})


