<template>
	<div style="margin-top: 80px;">
		<div v-if="g_arr.length">
			<div>
				<span style="width:20%;">商品</span>
				<span>价格</span>
				<span>数量</span>
				<span>合计</span>
			</div>
			<div>
				<div v-for="i in g_arr" style="line-height: 100px;overflow: hidden;margin: 10px 0;">
					<div style="float: left; width: 30px;text-align: center;" @click="schu(i.info)">X</div>
					<div style="float: left;width:100px;height:100px;margin-left: 30px;">
						<img :src="i.img" alt="" style="width:100px;height:100px;">
					</div>
					<div style="float: left;margin-left:50px; padding: 0 30px;">
						{{i.info}}
					</div>
					<div style="float: left;margin-left: 80px;padding: 0 8px;">
						￥ {{i.price}}
					</div>
					<div  style="float: left;margin-left: 120px; padding: 0 30px;">
					
						<button style="width:30px;height:35px;" @click="jian(i.info)">-</button>
							{{i.s}}
						<button style="width:30px;height:35px;" @click="jia(i.info)">+</button>
					</div>
					<div  style="float: left;margin-left: 150px;">
						￥ {{i.price*i.s}}
					</div>
				</div>
			</div>
			<div style="float:right">
				总价：{{zongji}}
			</div>
		</div>
		<div v-else style="line-height: 50px; text-align: center;">
			<p style="font-size: 16px; color: #777777">您的购物车目前是空的</p>
			<div style="background-color:#519f10;width: 100px;height: 50px;margin-bottom: 30px;margin-left: 46.5%;"><a href="#/no3" style="font-size: 16px;color: white;text-decoration: none;">返回商城</a></div>
		</div>
	</div>
	
</template>

<script>
	import {mapGetters,mapActions} from 'vuex'
	export default{
		computed:{
			...mapGetters(['g_arr','zongji'])
		},
		methods:{
			...mapActions(['jian','jia','schu'])
		}
	}
</script>

<style>
	*{
		margin: 0;
		padding: 0;
		
	}
	/*span{
		display: inline-block;
		padding: 0 20px;
	}*/
</style>



